import { IToDo } from "../interface";
import { updateFromLocalStorage } from "../localStorage";

const BASE_URL = "/api/todos";
const local = localStorage.getItem("TODOS") as string;

//HTTP status  500    offline 
/*
saveInLocalStroage : localStorage 
getFromLocalStroage : 
   id    toDo , 
    localStorage    
*/
export function saveInLocalStroage(toDo: IToDo) {
  const oldToDos = local === "[]" ? [] : JSON.parse(local);

  if (oldToDos.length == 0) {
    localStorage.setItem("TODOS", JSON.stringify([toDo]));
  } else if (oldToDos.length > 0) {
    localStorage.setItem("TODOS", JSON.stringify([...oldToDos, toDo]));
  }
}

export function getFromLocalStroage(id?: number) {
  const toDos: IToDo[] = local !== null ? JSON.parse(local) : [];

  //id   ( toDo )
  if (id !== undefined) {
    for (let toDo of toDos) {
      if (toDo.id === id) {
        return [toDo];
      }
    }
  }
  return [...toDos];
}

/*
updateFromLocalStorage :
500 (Server  offline  )   localStorage    
filter     ,    localStorage 

deleteFromLocalStorage :
500 (Server  offline  )   localStorage  toDo  
filter       toDo  localStorage 
*/

// toDo  api
export async function getAllToDo() {
  return await (
    await fetch(BASE_URL)
      .then((res) => res.json())
      .catch((error) => {
        console.log(error);

        if (error.status === 500) {
          return getFromLocalStroage();
        }
      })
  )();
}

// toDo  api
export async function getToDo(id: number) {
  return await (
    await fetch(`${BASE_URL}/${id}`)
      .then((res) => res.json())
      .catch((error) => {
        console.log(error);

        if (error.status === 500) {
          return getFromLocalStroage(id);
        }
      })
  )();
}

// toDo  api
//ID    .  ID    .

export async function createToDo(toDo: IToDo) {
  return await fetch(BASE_URL, {
    method: "POST",
    headers: {
      "Content-type": "application/json",
    },
    body: JSON.stringify({
      title: toDo.title,
      done: toDo.done,
      deadline: toDo.deadline,
    }),
  })
    .then((response) =>
      response.status === 200 ? console.log("Create Success") : false
    )
    .catch((error) => {
      console.log(error);

      if (error.status === 500) {
        return saveInLocalStroage(toDo);
      }
    });
}

export async function updateToDo(toDo: IToDo) {
  return await fetch(`${BASE_URL}/${toDo.id}`, {
    method: "PUT",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      title: toDo.title,
      done: toDo.done,
      deadline: toDo.deadline,
    }),
  })
    .then((response) =>
      response.status === 200 ? console.log("Update Success") : false
    )
    .catch((error) => {
      console.log(error);

      if (error.status === 500) {
        return updateFromLocalStorage(toDo);
      }
    });
}

export async function deleteToDo(toDo: IToDo) {
  return await fetch(`${BASE_URL}/${toDo.id}`, {
    method: "DELETE",
  })
    .then((response) =>
      response.status === 200 ? console.log("Delete Success") : false
    )
    .catch((error) => {
      console.log(error);

      if (error.status === 500) {
        //return deleteFromLocalStorage(toDo.id as any);
      }
    });
}
